import React from "react";
import {
    Dialog, DialogContent, Typography, IconButton, TextField,
    Button, DialogActions, Fab, CircularProgress, CardMedia,
    withStyles,
    withMobileDialog,
    AppBar, Toolbar, Hidden, Slide
} from "@material-ui/core";
import { Add, Remove, Close } from "@material-ui/icons";
import { withRouter } from "react-router-dom";
const styles = theme => ({
    dialogPaper: {
        maxWidth: 450,
        width: "100%",
        maxHeight: "calc(100% - 20px)",
        marginTop: 10,
        marginBottom: 10
    },
    loadingPaper: {
        marginLeft: 10,
        marginRight: 10,
        width: "calc(100% - 20px)",
        maxWidth: 450
    }
});

class ProductDetails extends React.Component {
    state = {
        nombre: "",
        descripcion: "",
        pvp_final: 0,
        cantidad: 1,
        imgSrc: "",
        nota: "",
        loading: true
    }
    constructor(props) {
        super(props);
        this.handleInput = this.handleInput.bind(this);
        this.handleDone = this.handleDone.bind(this);
        this.handleCantidad = this.handleCantidad.bind(this);
        this.handleExit = this.handleExit.bind(this);
        this.ID = props.match.params.id;
        if (props.choosenItems && props.choosenItems.length) {
            let item = props.choosenItems.find(x => x.id === (+this.ID));
            if (item) this.state = { ...item, loading: false };
        }
    }
    componentDidMount() {
        if (!this.state.loading) return;
        const { productList } = this.props;
        if (productList && productList.length) {
            const data = productList.find(x => x.id === (+this.ID));
            if (data)
                return this.setState({ loading: false, ...data });
        }
        alert("No se encontr el producto en la lista. Recargue la pantalla.");
    }
    handleCantidad(event) {
        const name = event.currentTarget.name;
        this.setState(prev => {
            const newState = { ...prev };
            let cantidad = newState.cantidad;
            if (name === "add") cantidad = (cantidad || 0) + 1;
            if (name === "remove") cantidad = cantidad <= 0 ? 0 : (cantidad - 1);
            newState.cantidad = cantidad;
            console.log("Product Details new cantidad ", cantidad);
            return newState;
        });
    }
    handleInput(event) {
        const { target } = event;
        this.setState({ [target.name]: target.value });
    }
    handleDone() {
        this.props.onDone({ ...this.state });
        this.handleExit();
    }
    handleExit() {
        const { match, history } = this.props;
        const new_path = match.path.substr(0, match.path.indexOf("/view-product/:id"))
        history.push(new_path);
    }
    render() {
        const { state } = this;
        const { classes, fullScreen } = this.props;
        const DialogProps = {
            fullScreen,
            scroll: "paper",
            sm: true,
            open: !state.loading
        };
        if (fullScreen) {
            DialogProps.TransitionComponent = Transition;
        } else {
            DialogProps.classes = { paper: classes.dialogPaper };
        }

        const pvp = ((state.pvp_final && state.cantidad)
            ? (state.pvp_final * state.cantidad)
            : state.pvp_final).toFixed(2);

        return (<React.Fragment>
            {state.loading && <Dialog open={true} classes={{ paper: classes.loadingPaper }}>
                <div style={{ marginTop: 100, marginBottom: 100, textAlign: "center" }}>
                    <CircularProgress size={30} />
                    <Typography variant="h5">Cargando...</Typography>
                </div>
            </Dialog>}
            <Dialog  {...DialogProps}>
                <Hidden smUp>
                    <AppBar position="static">
                        <Toolbar>
                            <Typography color="inherit" style={{ flexGrow: 1, textTransform: "uppercase" }} variant="h6">{state.nombre}</Typography>
                            <IconButton color="inherit" onClick={this.handleExit}>
                                <Close />
                            </IconButton>
                        </Toolbar>
                    </AppBar>
                </Hidden>

                <DialogContent style={{ padding: 0 }}>
                    <Hidden xsDown>
                        <Fab onClick={this.handleExit} color="secondary" style={{ float: "right", right: 10, top: 10, position: "absolute" }}>
                            <Close />
                        </Fab>
                    </Hidden>
                    <CardMedia className="product-details-img" image={state.imgSrc || "https://diegopq95.github.io/img/default.png"} title={state.nombre} />

                    <Hidden xsDown>
                        <div style={{ width: "100%", display: "flex", flexDirection: "row" }}>
                            <Typography style={{ margin: 10, flex: 1, textTransform: "uppercase" }} variant="title">{state.nombre}</Typography>
                            <Typography style={{ margin: 10 }} variant="title">$ {pvp}</Typography>
                        </div>
                    </Hidden>

                    <Typography style={{ margin: 10 }} variant="body2">
                        {state.descripcion}
                    </Typography>

                    <div style={{ width: "100%", textAlign: "center", marginTop: 20, marginBottom: 20, display: "table" }}>
                        <Hidden smUp>
                            <Typography style={{ verticalAlign: "middle", display: "table-cell" }} variant="h6">$ {pvp}</Typography>
                        </Hidden>

                        <div style={{ display: "table-cell", verticalAlign: "middle" }} >
                            <QuantityButton onClick={this.handleCantidad} name="add">
                                <Add />
                            </QuantityButton>
                            <Typography style={{ display: "inline-block", marginLeft: 20, marginRight: 20 }} variant="h6">
                                {state.cantidad || 0}
                            </Typography>
                            <QuantityButton disabled={state.cantidad <= 0} onClick={this.handleCantidad} name="remove">
                                <Remove />
                            </QuantityButton>
                        </div>
                    </div>

                    <div style={{ width: "95%", margin: "auto" }}>
                        <TextField value={state.nota}
                            inputProps={{ maxLength: 200 }}
                            onChange={this.handleInput}
                            name="nota"
                            color="default"
                            placeholder="Ej. Sin Salsa, verduras aparte, etc."
                            label="Agrega una nota:"
                            variant="outlined"
                            multiline rows="3" fullWidth />
                    </div>
                </DialogContent>
                <DialogActions>
                    <Button type="button" onClick={this.handleDone} color="primary" variant="outlined">AGREGAR</Button>
                </DialogActions>
            </Dialog >
        </React.Fragment >);
    }
}
export default withStyles(styles)(withRouter(withMobileDialog()(ProductDetails)));

function QuantityButton(props) {
    const style = { height: 50, width: 50, border: "1px solid gray" };
    if (props.disabled) delete style.border;
    return (<IconButton
        disabled={props.disabled}
        onClick={props.onClick}
        name={props.name} style={style}
    >
        {props.children}
    </IconButton>)
}

function Transition(props) {
    return <Slide direction="up" {...props} />;
}